import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { ITipoMovimiento } from '../models/tipo-movimiento';

@Injectable()
export class TipoDeMovimientosService {
    constructor(private http: HttpClient) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Tipos de movimientos.
     * @returns Tipos de movimientos
     */
    getTiposMovimientos(sort?:any): Observable<ITipoMovimiento[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        return this.http.get<ITipoMovimiento[]>(`${environment.fondos.fondos_tipo}${f}`);
    }

    /**
     * Funcion en el Servicio 
     * @param tipo_mov 
     * @returns Un tipo de movimiento nuevo nuevo
     */
    saveTipoMovimiento(tipo_mov: ITipoMovimiento): Observable<ITipoMovimiento> {
        return this.http.post<ITipoMovimiento>(`${environment.fondos.fondos_tipo}`, tipo_mov);
    }

    /**
     * Funcion en el servicio
     * @param tipo_mov
     * @returns Tipo movimiento actualizado
     */
    updateTipoMovimiento(tipo_mov: ITipoMovimiento): Observable<ITipoMovimiento> {
        return this.http.put<ITipoMovimiento>(`${environment.fondos.fondos_tipo}` + '/' + tipo_mov.idtipo, tipo_mov);
    }

    /**
     * Funcion en el servicio
     * @param idtipo 
     * @returns Elimina el tipo de movimiento
     */
    deleteTipoMovimiento(idtipo: number): Observable<ITipoMovimiento> {
        return this.http.delete<ITipoMovimiento>(environment.fondos.fondos_tipo + '/' + idtipo);
    }
}